import React from 'react';
import { Gallery, GalleryImage } from 'react-gesture-gallery';
import './Home.css';
import Footer from '../footer/Footer';

const sliders = [ {
  image: "img/slider1.jpg",
  header: "New collection Coming to town",
  text:     "Introducing fashionable & gorgeous iMac\n" +
  "From design to simple use",
  button: "Read More",
},
  {
    image: "img/slider2.jpg",
    header: "New collection Coming to town",
    text:     "Introducing fashionable & gorgeous iMac\n" +
      "From design to simple use",
    button: "Read More",
  },
  {
    image: "img/slider3.jpg",
    header: "New collection Coming to town",
    text:     "Introducing fashionable & gorgeous iMac\n" +
      "From design to simple use",
    button: "Read More",
  },
];

const HomePage = () => {
  const [index, setIndex] = React.useState(0);
  React.useEffect(() => {
    const timer = setInterval(() => {
      if (index === 2) {
        setIndex(0);
      } else {
        setIndex(prev => prev + 1);
      }
    }, 5000);

    return () => clearInterval(timer);
  }, [index]);

  const headers = sliders.map(header => (
      <h1> {header.header} </h1>
  ));
  const texts = sliders.map(text => (
      <h2> {text.text} </h2>
  ));
  const buttons = sliders.map(button => (
      <button className="home__slider--button"> {button.button} </button>
  ));
  const img = sliders.map(slider => (
    <div>
      <div>
        <GalleryImage objectFit="contain" key={slider.image + new Date().getTime} src={slider.image} />
        {headers[index]}
        {texts[index]}
        {buttons[index]}
      </div>
    </div>
  ));

  return (
    <>
    <div className="home">
      <Gallery
        className="home__slider"
        index={index}
        onRequestChange={(i) => {
          setIndex(i);
        }}
      >
        {img}
      </Gallery>

      <div className="home__features">
        <div className="home__features--about wrapper">
          <div className="home__features--about--block">
            <h4 className="home__features--about--block--title">Home shipping</h4>
            <h6 className="home__features--about--block--text">Free for all order</h6>
          </div>
          <div className="home__features--about--block">
            <h4 className="home__features--about--block--title">100% Refund</h4>
            <h6 className="home__features--about--block--text">Cash Back</h6>
          </div>
          <div className="home__features--about--block">
            <h4 className="home__features--about--block--title">Clients′ Support</h4>
            <h6 className="home__features--about--block--text"> Fast Service </h6>
          </div>
          <div className="home__features--about--block">
            <h4 className="home__features--about--block--title">Fast Delivery</h4>
            <h6 className="home__features--about--block--text"> Best Service </h6>
          </div>
        </div>
      </div>
      <div className="home__goods wrapper">
          <div className="home__goods--block">
            <div className="home__goods--block--text">
              New Step
            </div>
            <div className="home__goods--block--name">
              Apple iPhone
            </div>
            <div className="home__goods--block--name">
              <pre> </pre>
            </div>
            <div className="home__goods--block--startprice">
              Starting at
            </div>
            <div className="home__goods--block--price">
              $599
            </div>
          </div>
          <div className="home__goods--block">
            <div className="home__goods--block--text">
              One Shot
            </div>
            <div className="home__goods--block--name">
              iMac Pro Xeon Radeon Vega
            </div>
            <div className="home__goods--block--startprice">
              Starting at
            </div>
            <div className="home__goods--block--price">
              $5199
            </div>
          </div>
      </div>
    </div>
    <Footer />
    </>
  );
};

export default HomePage;
