import React from 'react';
import { getPhones } from '../../getData';
import Phone from './Phone';
import Filter from '../filter/Filter';

class PhoneCatalog extends React.Component {
  constructor() {
    super();
    this.state = {
      phones: [],
      isLoaded: false,
      isLoading: false,
    };
  }

  componentDidMount() {
    this.loadData();
  }

  loadData = async() => {
    const phones = await getPhones();
    const preparedPhones = phones.map(phone => ({
      ...phone,
    }
    ));

    this.setState({
      isLoading: true,
      phones: [...preparedPhones],
      visiblePhones: preparedPhones,
    });
    this.setState(prevState => ({
      isLoaded: true,
      isLoading: false,
    }));
  };

  handleSearch = (event) => {
    const search = event.target.value;

    this.setState(prevState => ({
      phones: prevState.visiblePhones.filter(
        phone => [phone.name]
          .join('').toLowerCase().includes(search.toLowerCase())
      ),
    }));
  };

  sortPhone = (event) => {
    const {value} = event.target;

    switch (value) {
      case 'alphabetical':
        this.setState(prevState => ({
          phones: prevState.phones
            .sort((a, b) => a.name.localeCompare(b.name)),
        }));

        break;
      case 'newest':
        this.setState(prevState => ({
          phones: prevState.phones.sort((a, b) => a.age - b.age),
        }));

        break;
      default:
        this.setState(prevState => ({
          visiblePhones: prevState.visiblePhones,
        }));
    }
  };

  render() {

    if (!this.state.isLoaded) {
      return (
        <div className="text-center">
          {this.state.isLoading ? (
            <div className="spinner-border" role="status" />
          )
            : (
              <div className="spinner-border mt-5" role="status">
                <span className="sr-only">Loading...</span>
              </div>
            )
          }
        </div>
      );
    }

    return (
      <div>
        <div>
          <Filter sortPhone={this.sortPhone} />
        </div>
        <form className="form-horizontal">
          <input
            placeholder="Search..."
            onChange={this.handleSearch}
            className="form-control mb-4"
          />
        </form>
      </div>
    );
  }
}

export default PhoneCatalog;
