import React from 'react';
import PropTypes from 'prop-types';
import { NavLink, withRouter } from 'react-router-dom';
import './Phone.css';

const Phone = ({ phones, history }) => {

  const addToCart = (id, name) => {
    if (localStorage.getItem('buy')) {
      localStorage.buy += `&${id}*1`;
    } else {
      localStorage.buy = `${id}*1`;
    }

    localStorage.setItem(id, name);

    history.replace(history.location);
  };

  const listOfPhones = phones.map(phone => (
    <div className="phone-catalogue__phone" key={phone.age}>
      <div className="phone-catalogue__phone--img">
        <img src={`https://githubfordmitry.github.io/react_phone-catalog/${phone.imageUrl}`} alt={phone.id} />
        <NavLink
          className="phone-catalogue__phone--link"
          to="/"
        >
          {phone.name}
        </NavLink>
        <p
          className="phone-catalogue__phone--snippet"
        >
          {phone.snippet}
        </p>
      </div>
      <button
        type="button"
        disabled={localStorage.getItem(phone.id) && true}
        className="phone-catalogue__phone--buy"
        onClick={() => addToCart(phone.id, phone.name)}
      >
        {
          localStorage.getItem(phone.id)
            ? 'Added to basket'
            : 'Buy'
        }
      </button>
    </div>
  ));

  return listOfPhones;
};

Phone.propTypes = {
  phones: PropTypes.shape({
    age: PropTypes.number.isRequired,
    id: PropTypes.string.isRequired,
    name: PropTypes.string.isRequired,
    imageUrl: PropTypes.string.isRequired,
    snippet: PropTypes.string.isRequired,
  }).isRequired,
};

Phone.propTypes = {
  phones: PropTypes.arrayOf(PropTypes.object).isRequired,
};

export default withRouter(Phone);
