import React from 'react';
import PropTypes from 'prop-types';
import PhoneCatalog from '../phoneCatalog/PhoneCatalog';
import { getPhones } from '../../getData';
import Phone from '../phoneCatalog/Phone';

class PhonePage extends React.Component {
  state = {
    phones: [],
  };

  componentDidMount() {
    getPhones().then(
      (data) => {
        this.setState({
          phones: [...data],
        });
      }
    );
  }

  render() {
    const { phones, match, history } = this.state;

    return (
      <>
        <div className="phone-catalogue">
          <Phone
            phones={phones}
            match={match}
            hisotry={history}
          />
        </div>
        <PhoneCatalog />

      </>
    );
  }
}

PhonePage.propTypes = {
  location: PropTypes.shape({
    search: PropTypes.string,
  }).isRequired,
};

export default PhonePage;
